import React from 'react';
import { BrowserRouter as Router,Redirect, Route, Switch } from 'react-router-dom'
import Header from './Header';
import routes from './router.config';


const RouteWithSubRoutes=(props)=>{
    if(props.pri){
        return(
            <PrivateRoute
                path={props.path}
                component={props.component}
                routes={props.routes}
            />
        );//return
    }else{
        return(
            <Route
                path={props.path}
                render={rest=>{
                    return <props.component {...rest} routes={routes} />
                }}
            />
        );//return
    }
}

const PrivateRoute=({component:Com,...rest})=>{
    return(
        <Route
            {...rest}
            render={({location})=>{
                return (
                    localStorage.getItem('userinfo')?(
                        <Com {...rest} />
                    ):(
                        <Redirect 
                            to={{
                                pathname:'/login',
                                state:location
                            }}
                        />//Redirect
                    )
                );//return        
            }}//render
        />//Route
    );//return
}

const App=()=>{
	return(
        <Router>
            <Header />
		    <Switch>
                {
                    routes.map((route,i)=>{
                        return(
                            <RouteWithSubRoutes
                                key={i}
                                {...route}
                            />
                        )
                    })//map
                }
		    </Switch>
	    </Router>
    );//return
}
export {RouteWithSubRoutes,App};