import React from 'react'

const Home=(props)=>{
    return (
        <div style={{textAlign:'center'}}>
            <h1>
                React
            </h1>
            <p>
                用于构建用户界面的JavaScript库
            </p>
            <div>
                <button
                    onClick={()=>props.history.push('/doc')}
                >
                    快速开始
                </button>
                <button
                    onClick={()=>props.history.push('/tuto')}
                >
                    入门教程
                </button>
            </div>
        </div>
    )
}

export default Home;